//Global reset
body, * {
  box-sizing: border-box;
  vertical-align: middle;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  font-size: 100%;
  border: 0 none;
  outline: 0;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  max-width: 100vw;
  overflow-x: auto;
  overflow-y: auto;
  background-color: $black;

  &, p {
    @include inputText;
    white-space: pre-line;

    &.bold, &>.bold {
      font-weight: $heavy;
    }
  }

  h1, h2, h3 {
    font-family: $roboto;
    white-space: pre-line;
  }

  .title {
    @include title;
  }

  .subtitle {
    @include subtitle;
  }

  //hidden label with accessibility
  label {
    z-index: -1;
    position:absolute;
    left:0;
    top:auto;
    width:1px;
    height:1px;
    overflow:hidden;
  }

  input {
    @include inputText;

    appearance: none;
    box-sizing: border-box;
    border: none;
    border-radius: $rem4;
    padding: $rem6;
    margin: $rem4;

    &::placeholder {
      @include inputText;
    }

    &:focus {
      outline: none;
    }
  }

  textarea {
    @include inputText;

    resize: none;
    width: 100%;
    appearance: none;
    box-sizing: border-box;
    border: none;
    border-radius: $rem4;
    padding: $rem6;
    margin: $rem4;

    &::placeholder {
      @include inputText;
    }
  }

  a {
    font-family: $source-sans-pro;
    font-size: $rem24;
    font-weight: $regular;
    color: $red;
    text-decoration:none;

    &::hover {
      cursor: pointer;
    }
  }

  button, .button {
    @include inputText;

    width: auto;
    margin: $rem8 auto;
    padding: $rem8;
    background: $red;
    border: 1px solid $red;
    border-radius: $rem16;
    color: $cream;
    font-family: $source-sans-pro;
    font-weight: $regular;
    font-size: $rem20;
    cursor: pointer;

    &.icon {
      padding: $rem20 75px $rem20 $rem8;

      &::after {
        top: calc(50% - #{$rem24});
        right: $rem16;
        height: 50px;
        width: 50px;
      }
    }
  }

  ul, ol {
    list-style-type: none;

    &>li {
      @include inputText;

      list-style: none;
    }
  }

  .icon {
    position: relative;

    &::after {
      position: absolute;
      content: '';
      z-index: 1;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &.envelope-dots::after {
      background-image: url('../../images/icons8-envelope-dots-100.png');
    }

    &.octocat::after {
      background-image: url('../../images/GitHub-Mark/PNG/GitHub-Mark-120px-plus.png');
    }

    &.bitbucket::after {
      background-image: url('../../images/bitbucket-atlassian-logo.svg');
    }

    &.linkedin::after {
      background-image: url('../../images/LinkedIn-Logos/LI-In-Bug.png');
    }

    &.betacanon::after {
      background-image: url('../../images/beta-canon-logo-mark.svg');
    }

    &.golowpro::after {
      background-image: url('../../images/logo-glp.png');
    }

    &.textual::after {
      background-image: url('../../images/client-logo-textual.svg');
    }

    &.kagedmuscle::after {
      background-image: url("../../images/client-logo-kaged-muscle.svg");
    }
  }
}

